/* Provide sufficient contrast against white background */
a {
  color: #0366d6;
}

code {
  color: #E01A76;
}

.btn-primary {
  color: #fff;
  background-color: #1b6ec2;
  border-color: #1861ac;
}
.input-text {
    font-size: 1rem;
    font-weight: 100;
    width: 20%;
    padding-right: 5px;
    padding-left: 10px;
    border-radius: calc(0.5 * 100px);
}
.input-dropdown {
    font-size: 1rem;
    font-weight: 100;
    width: 10%;
    padding-right: 5px;
    padding-left: 10px;
    border-radius: calc(0.5 * 100px);
}
.form-container {
    width: 100%;
    margin-bottom: 20px;
    display: flex;
    border-radius: calc(0.5 * 100px);
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.38);
    justify-content: space-evenly;
}
.input-submit {
    font-size: 13px;
    background: #1861ac;
    border: none;
    cursor: pointer;
    float: right;
    outline: none;
    border-radius: 100px;
    height: 25px;
    width: 70px;
    margin: 3px 0 0 8px;
}
.input-submit-more {
    font-size: 13px;
    background: #1861ac;
    border: none;
    cursor: pointer;
    float: right;
    outline: none;
    border-radius: 100px;
    height: 25px;
    width: 70px;
    margin: 3px 530px 0 1px;
}
